import React from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';

export function NestedSelect(props) {
	const typesnns=[
		"enet-coco",
		"yolov3/coco",
		"yolov3/drone",
		"yolov3/openimages",
		"yolov3-tiny/coco",
		"yolov3-tiny/drone",
		"yolov3-spp/coco",
		"yolov3-spp/drone/spp1",
		"yolov3-spp/drone/spp3",
		"yolov3-spp-slim/drone/prune_0.5",
		"yolov3-spp-slim/drone/prune_0.5_0.5_0.7",
		"yolov3-spp-slim/drone/prune_0.9",
		"yolov3-spp-slim/drone/prune_0.95",
		"ttfnet/coco/ttfnet_d53_1x",
		"ttfnet/coco/ttfnet_d53_2x",
		"ttfnet/coco/ttfnet_r18_1x",
		"ttfnet/coco/ttfnet_r18_2x",
		"ttfnet/coco/ttfnet_r34_2x",
	];
	return(
		<React.Fragment>
			<InputLabel htmlFor="type-helper1">{props.namenn}</InputLabel>
			<Select
				value={props.nn_type.type}
				onChange={props.handleChangenns}
			>
				<MenuItem value="">
				<em>None</em>
				</MenuItem>
				<MenuItem value={0}>{typesnns[0]}</MenuItem>
				<MenuItem value={1}>{typesnns[1]}</MenuItem>
				<MenuItem value={2}>{typesnns[2]}</MenuItem>
				<MenuItem value={3}>{typesnns[3]}</MenuItem>
				<MenuItem value={4}>{typesnns[4]}</MenuItem>
				<MenuItem value={5}>{typesnns[5]}</MenuItem>
				<MenuItem value={6}>{typesnns[6]}</MenuItem>
				<MenuItem value={7}>{typesnns[7]}</MenuItem>
				<MenuItem value={8}>{typesnns[8]}</MenuItem>
				<MenuItem value={9}>{typesnns[9]}</MenuItem>
				<MenuItem value={10}>{typesnns[10]}</MenuItem>
				<MenuItem value={11}>{typesnns[11]}</MenuItem>
				<MenuItem value={12}>{typesnns[12]}</MenuItem>
				<MenuItem value={13}>{typesnns[13]}</MenuItem>
				<MenuItem value={14}>{typesnns[14]}</MenuItem>
				<MenuItem value={15}>{typesnns[15]}</MenuItem>
				<MenuItem value={16}>{typesnns[16]}</MenuItem>
				<MenuItem value={17}>{typesnns[17]}</MenuItem>
			</Select>
		</React.Fragment>
	);
}
